import {Col, Container} from "reactstrap";
import TextLoop from "react-text-loop";
import React, {useState} from "react";

export default function Post({children, video, title}) {

    const [active, setActive] = useState('text')
    let goToVideo = <div className={"minimize"} onClick={() => {
        setActive('video')
    }}/>


    let goToText = <div className={"maximize"} onClick={() => {
        setActive('text')
    }}/>
const textDisplay=active === 'text' ? 'block' : 'none';
    return <div>
        <div id="video_box">
            <div id={`${video}-text`} className={`video_overlays ${active === 'text' ? 'active' : ''} timeout`}
                     style={{display:textDisplay}}>
                <center><TextLoop interval={2000} width="640" height="480">
                    {children}
                </TextLoop></center>
                {goToVideo}
            </div>
 
            <div>
                <a href={video}>Watch In YouTube</a>

                <iframe width="560" height="315" src={`https://www.youtube.com/embed/${video}`}
                        title="YouTube video player" frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowFullScreen/>
            

            </div>
        </div>

    </div>

}

function Post2({children, video, title}) {

    const [active, setActive] = useState('text')
    let goToVideo = <div className={"close"} onClick={() => {
        setActive('video')
    }}>Go to Video</div>


    let goToText = <div onClick={() => {
        setActive('text')
    }}>Go to Titles</div>

    return <div>
        <h2 className="major">{title}</h2>
        <div id={video} style={{display: 'flex'}}>

            <article id={`${video}-text`} className={`${active === 'text' ? 'active' : ''} timeout`}
                     style={{display: 'none'}}>

                <div>
                    {goToVideo}
                    <TextLoop interval={2000} width="640" height="480">

                        {children}

                    </TextLoop>

                </div>
            </article>

            <article id={`${video}-vid`} className={`active timeout`}
                     style={{display: 'none'}}>
                <video width="640" height="480" controls>
                    <source src={video} type="video/mp4"/>
                    {goToText}

                </video>
            </article>

        </div>

    </div>

} 